import Vue from 'vue'
import Router from 'vue-router'
import AuthLayout from '../components/auth/AuthLayout'
import AppLayout from '../components/admin/AppLayout'

Vue.use(Router)

function guardMyroute (to, from, next) {
  var isAuthenticated = false
  if (localStorage.getItem('LoggedUser')) { isAuthenticated = true } else { isAuthenticated = false }
  if (isAuthenticated) {
    next() // allow to enter route
  } else {
    next('/auth/login') // go to '/login';
  }
}

const EmptyParentComponent = {
  template: '<router-view :key="$route.fullPath"></router-view>',
}

const demoRoutes = []

export default new Router({
  mode: process.env.VUE_APP_ROUTER_MODE_HISTORY === 'true' ? 'history' : 'hash',
  routes: [
    ...demoRoutes,
    {
      path: '*',
      redirect: { name: 'login' },
    },
    
    {
      path: '/auth',
      component: AuthLayout,
      children: [
        {
          name: 'login',
          path: 'login',
          component: () => import('../components/auth/login/Login.vue'),
        },        
        {
          name: 'signup',
          path: 'signup',
          component: () => import('../components/auth/signup/Signup.vue'),
        },
        {
          name: 'recover-password',
          path: 'recover-password',
          component: () => import('../components/auth/recover-password/RecoverPassword.vue'),
        },
        {
          path: '',
          redirect: { name: 'login' },
        },
      ],
    },       
    {
      name: 'Admin',
      path: '/admin',
      component: AppLayout,
      children: [
        {
          name: 'card-response',
          path: 'card-response',             
          component: () => import('../components/member-activities/CardResponse.vue'),
          props(route) {
            return  route.query || {}
          }
        },
        {
          name: 'dashboard',
          path: 'dashboard',
          beforeEnter: guardMyroute,
          component: () => import('../components/dashboard/Dashboard.vue'),
          default: true,
        },
        {
          name: 'member-reports',
          path: 'member-reports',
          component: EmptyParentComponent,
          children: [
            {
              name: 'MEMBSTMNT',
              path: 'MEMBSTMNT',
              beforeEnter: guardMyroute,
              component: () => import('../components/pdf/Test.vue'),
              meta: {
                wikiLink: 'https://github.com/epicmaxco/vuestic-admin/wiki/inputs',
              },
            },
            {
              name: 'Payments',
              path: 'Payments',
              component: () => import('../components/pdf/payments.vue'),
              meta: {
                wikiLink: 'https://github.com/epicmaxco/vuestic-admin/wiki/Medium-Editor',
              },
            },
            {
              name: 'Invoices',
              path: 'Invoices',
              beforeEnter: guardMyroute,
              component: () => import('../components/pdf/invoices.vue'),
              meta: {
                wikiLink: 'https://github.com/epicmaxco/vuestic-admin/wiki/Medium-Editor',
              },
            },
            {
              name: 'Credit-Notes',
              path: 'Credit-Notes',
              beforeEnter: guardMyroute,
              component: () => import('../components/pdf/credit-notes.vue'),
              meta: {
                wikiLink: 'https://github.com/epicmaxco/vuestic-admin/wiki/Medium-Editor',
              },
            },
            {
              name: 'Proforma-Invoice',
              path: 'proforma-invoice',
              beforeEnter: guardMyroute,
              component: () => import('../components/pdf/proforma-invoice.vue'),
              meta: {
                wikiLink: 'https://github.com/epicmaxco/vuestic-admin/wiki/Medium-Editor',
              },
            },
            {
              name: 'LOANSTMNT',
              path: 'LOANSTMNT/:loan_no',
              beforeEnter: guardMyroute,
              component: () => import('../components/pdf/Test.vue'),
              meta: {
                wikiLink: 'https://github.com/epicmaxco/vuestic-admin/wiki/Medium-Editor',
              },
            },
          ],
        },
        {
          name: 'loans-center',
          path: 'loans-center',
          component: EmptyParentComponent,
          children: [
            {
              name: 'active-loans',
              path: 'active-loans',
              beforeEnter: guardMyroute,
              component: () => import('../components/loans/activeloans.vue'),
            },
            {
              name: 'loan',
              path: 'loan/:loan_no',
              beforeEnter: guardMyroute,
              component: () => import('../components/loans/loan.vue'),
            },
          ],
        },
        {
          name: 'member-activities',
          path: 'member-activities',
          component: EmptyParentComponent,
          children: [
            {
              name: 'deposit-funds',
              path: 'deposit-funds',
              beforeEnter: guardMyroute,
              component: () => import('../components/member-activities/DepositFunds.vue'),
            },
            {
              name: 'funds-transfer',
              path: 'funds-transfer',
              beforeEnter: guardMyroute,
              component: () => import('../components/member-activities/FundsTransfer.vue'),
            },
            {
              name: 'pass-reset',
              path: 'pass-reset',
              beforeEnter: guardMyroute,
              component: () => import('../components/member-activities/CashWithraw.vue'),
            },
            {
              name: 'loans-payment',
              path: 'loans-payment',
              beforeEnter: guardMyroute,
              component: () => import('../components/loans/payloans.vue'),
            },
            {
              name: 'generate-proforma',
              path: 'generate-proforma/:inv_num',
              beforeEnter: guardMyroute,
              component: () => import('../components/pdf/proforma.vue'),
            },
            {
              name: 'create-proforma',
              path: 'create-proforma',
              beforeEnter: guardMyroute,
              component: () => import('../components/pdf/proforma.vue'),
            },
            {
              name: 'mobile-loan',
              path: 'mobile-loan',
              beforeEnter: guardMyroute,
              component: () => import('../components/loans/mobileloan.vue'),
            },            
            {
              name: 'buy-airtime',
              path: 'buy-airtime',
              beforeEnter: guardMyroute,
              component: () => import('../components/vas/buyairtime.vue'),
            },
          ]
        },
        {
          name: 'change-password',
          path: 'change-password',
          beforeEnter: guardMyroute,
          component: () => import('../components/user/ChangePin.vue')            
        }
      ],
    },
  ],
})
